import Vue from 'vue'
import Vuex from 'vuex'

import * as acts from './constants/types.actions.js'
import * as muts from './constants/types.mutations.js'
import * as gets from './constants/types.getters.js'

import * as gen from './api/api.gens.js'

import * as api_point from './api/api.points.js'

Vue.use(Vuex)

const state = {
  points: [],
  events: [],
  users: [],
}

const mutations = {
  [muts.SET_POINT](state, point) {
    console.log('## inside mutations: ', point);
    state.points.push(point);
  },
}

const actions = {
  [acts.SET_RND_POINTS]({
    commit
  }) {
    console.log('## inside action: ', commit);
    const rnd_point = {
      name: gen.token(4),
      coordinates: [gen.num(), gen.num()],
    };
    commit(muts.SET_POINT, rnd_point);
  },
  [acts.GET_RND_POINT]({
    commit
  }) {
    api_point.getRndPoint.get().then(res => {
      console.log('## res: ', res.data.body);
      const point = {
        name: res.data.body.Token,
        coordinates: res.data.body.Coordinates,
      };
      commit(muts.SET_POINT, point);
    });
  },
}

// getters are functions
const getters = {
  [gets.GET_POINTS](state) {
    return state.points;
  },
}

// A Vuex instance is created by combining the state, mutations, actions,
// and getters.
export default new Vuex.Store({
  state,
  getters,
  actions,
  mutations
})



