<template>
  <div id="app">
	<div class="main-panel">
  <b-navbar toggleable type="inverse" variant="inverse" class="dvi-navbar collapse">
  <b-collapse isNav id="nav_collapse">
  <b-nav isNavBar>
    <b-nav-item>dvij</b-nav-item>
    <router-link to="/users"><b-nav-item>users</b-nav-item></router-link>
		<router-link to="/events"><b-nav-item>events</b-nav-item></router-link>
		<router-link to="/groups"><b-nav-item>groups</b-nav-item></router-link>
  </b-nav>
  </b-collapse>
  </b-navbar>

  <!-- <router-view class="view"></router-view> -->
  </div>
	<div class="container debug-panel">
		<h1>DEBUG PANEL</h1>
		<div>
      <b-button :size="size" :variant="variant"
       class="btn btn-primary" @click="GET_RND_POINT">get rnd points</b-button>
       <b-button :size="size" :variant="variant"
       class="btn btn-primary" @click="SET_RND_POINTS">set rnd points</b-button>
	  </div>
  <button @click="GET_RND_POINT">get rnd point</button>
  <div>{{ GET_POINTS }}</div>
  <button v-on:click="addEvents()">addEvents</button>
  <button @click="showPoints()">showPoints</button>
		dfdsffadsfasdf
	</div>
  <div class="google-map" id="map"></div>
  </div>
</template>

<script>
  import loadGoogleMapsAPI from 'load-google-maps-api';

  import { mapGetters, mapActions } from 'vuex'
  import * as gets from '../constants/types.getters.js'
  import * as acts from '../constants/types.actions.js'

  import InfoWindow from './infowindow.vue'

  // import api_points from '../components/api.points';
  export default {
    data(){
      return {
      }
    },
    components: {
      InfoWindow,
    },
    mounted: function()
    {
      console.log('gets: ', gets.GET_POINTS);
      const my_key = 'AIzaSyCbtCG0K8Tq9tpdljYkW7i-gxzFD8FvPi8';
      loadGoogleMapsAPI({key: my_key}).then((googleMaps) => {
        //console.log(googleMaps);
        //console.log(window.google);
        return;
      }).then(() => {
        this.initMap();
      });
    },
    computed: {
      ...mapGetters([
        gets.GET_POINTS,
      ]),
    },
    methods: {
      ...mapActions([
        acts.SET_RND_POINTS,
        acts.GET_RND_POINT,
      ]),
      showPoints: function(){
        let collection = this.$store.state.points;
        let pointMarker = new Array();

        for(let i = 0; i < collection.length; i++){
          pointMarker[i] = new google.maps.Marker({
            position: new google.maps.LatLng(collection[i].coordinates[0], collection[i].coordinates[1]),
            map: this.map,
            animation: google.maps.Animation.BOUNCE,
            title: "collection"+ collection[i].name
          });
        }
      },
      initMap: function(){
        const myLatLng = {lat: -25.363, lng: 131.044};
        const map = new google.maps.Map(document.getElementById('map'), {
          zoom: 4,
          center: myLatLng
        });
        this.map = map;

        const marker = new google.maps.Marker({
          id: 1,
          position: myLatLng,
          map: map,
          title: 'Hello World!'
        });
        marker.addListener('click', showInfo);
        google.maps.event.addListener(map, 'click', function(e) {
          console.log('##clicked: ',e.latLng);
        });
        function showInfo() {
          console.log('##elem: ', this);
          const infowindow = new google.maps.InfoWindow({
            content: 'something',
          });
          infowindow.open(map, marker);
        }
      },
      addEvents: function(){
        const myLatLng = {lat: -45.363, lng: 131.044};
        const marker = new google.maps.Marker({
          position: myLatLng,
          title: 'new marker',
          draggable: true,
          map: this.map
        });
        this.map.setCenter(marker.getPosition());
        console.log(`##here`);
      },
    }
  }
</script>

<style>
  html,
  body {
  }
	.main-panel {
    width: 100%;
    position: absolute;
    z-index: 2;
	}
  .google-map {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 0;
  }
	.debug-panel {
    width: 300px;
    height: 300px;
		top: 20%;
    right: 1%;
    position: absolute;
		float: right;
    z-index: 2;
		background: azure;
	}

</style>



